// This acts as a  Individual Model for Blogs 


import axios from 'axios';

const state={
  selectedBlog:null,
  blogs:[
    {
      id:1,
      title:"Nepal EV Future",
      desc:'With growing interest of Electric vehicles in the global market,Nepal is also pushing Forward towards Electric vehicle future',
      body:"<b>1.Introduction</b><p>The major cities in the world along with Nepal have faced the problem of air pollution affecting the lives of people. Transportation is the major cause for the existing air pollution since the most used vehicles are fossil fuel based two wheelers and four wheelers. The fossil fuel-based vehicles are resulting in a devastating effect to the environment which leads to damage in peopleâs lives too. On January 5 of the year 2021, the Air Quality index of Kathmandu hit the highest record crossing 500 (denotes hazardous condition) in which the major factor contributing to the degrading quality of air is vehicular emission. EVs (Electric vehicles) could be the great sustainable alternative which will contribute to the improvement of the environment and also can be an economically viable option.</p><b>1.1.	Background</b><p>The public EVs have been around for decades, while private EVâs are being popular in the present scenario of the country. In 1993, safa-tempos (battery-powered three-wheelers) were introduced in Kathmanduâs streets to replace the city's diesel-powered three-wheelers which reached its peak of popularity at the time along with expansion up to 700 safa-tempos, establishment of seven tempo manufacturing plants and 38 charging stations. The project was initiated by Global Resource institute, a non-profit with funding by USAID. Later safa-tempos faced the downfall around 2009-10 due to various causes like inconsistent government policies, competition to the introduction of microbuses, high battery costs and load shedding problems. In 2019, few electric buses were also introduced for public transportation which was mainly implemented in the Ring Road route of Kathmandu valley. For private EVs, there are better choices and ranges in terms of electric models of vehicles at present times than a decade ago. Companies like Hyundai, KIA and many others are launching electric cars with powerful battery life and better ranges. In the case of two wheelers, the electric scooters and electric bikes are also in the choice of many consumers with a wide range of brands and varieties to choose from.</p>"+
      
      "<b>3.	Current Situation</b>"+
      "<p>With growing interest of Electric vehicles in the global market, many of the worldsâ developed countries have been able to push the use of electric vehicles. Campaign guided by both the government and the automobile owners, developed countries like USA, England, New Zealand have been able to push the use of electronic vehicles. In neighbouring country India, the government has plans to make a major shift to electric vehicles so as to tackle the issue of pollution as well as to grow the manufacturing capabilities of the country. The government expects the electric two and three-wheeler segment will drive India's shift to electric mobility. By 2025, forecasts expect up to 4 millions of such vehicles (EV) could be sold each year, growing to almost 10 million by 2030.</p>"+
      
      "<p>In context to Nepal, data from the Government offices show high use of petroleum-based vehicles. Even though different governmental charges are added while buying petroleum vehicles while decreasing the charges in electric vehicles, the Government has failed to encourage the use of electric vehicles. Also, Nepal Electricity Authority (NEA) has signed a contract with Chinese firm Wanwang Digital Energy Corporation for the installation and maintenance of 50 e-vehicle charging ports across Nepal. In Spite of these efforts from the Government, auto companies have failed to lure customers compared to petroleum-based vehicles. Considering different reasons like unavailability of spare parts, weak human resources, no known brands, this lower attraction to electric vehicles seems reasonable. Only small amount, in range of 45000, of total sum of vehicles are electric vehicles as per the Electric Vehicle Association of Nepal (EVAN).</p>"
    },
  ]
};

const getters={
  allBlogs:(state)=>state.blogs,
  selectedBlog:(state)=>state.selectedBlog
};

const actions={
  async fetchBlogs({commit},count=50){
    const response=await axios.get('https://jsonplaceholder.typicode.com/postsall');
    console.log(response.data);
    response.data.length=count
    commit('setBlogs',response.data)
  },
  async getBlog({commit},id){

    const blog=state.blogs.filter((blog)=>{
      return blog.id==id.id;
    })

    commit('setSelectedBlog',blog)
  }
  
};


const mutations={
  setBlogs:(state,blogs)=>{
    if(blogs.length!=0){
      state.blogs=blogs
    }
  },
  setSelectedBlog:(state,blog)=>{
    state.selectedBlog=blog
  }

};


export default{
  state,
  getters,
  actions,
  mutations
}