<script>
import { Howl } from "howler";
export default {
  name: "Footer",
  data() {
    return {
      player: null,
      theme: "",
    };
  },
  mounted() {
    let localTheme = localStorage.getItem("theme"); //gets stored theme value if any
    document.documentElement.setAttribute("data-theme", localTheme); // updates the data-theme attribute
  },
  computed: {
    isPlaying() {
      return this.player && this.player.playing();
    },
  },
  methods: {
    toggleAudioPlay() {
      let path = require("/public/sound/nightmode.mp3");
      if (!this.player) {
        this.player = new Howl({
          src: [path],
          loop: true,
          volume: 0.1,
          onloaderror: (err) => {
            if (err) {
              console.log("Please Provide Feedback.Will Look Into it");
            }
          },
        });
      }
      if (this.isPlaying) {
        this.player.pause();
      } else {
        this.player.play();
      }
    },

    toggleTheme() {
      this.theme = this.theme == "lightMode" ? "" : "lightMode"; //toggles theme value
      document.documentElement.setAttribute("data-theme", this.theme); // updates the data-theme attribute
      localStorage.setItem("theme", this.theme); // stores theme value in local storage
    },
  },
};
</script>


